
import React , {FunctionComponent} from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import Divider from '@material-ui/core/Divider';
import { useSelector } from 'react-redux';
import { RootState } from '../store';
import Badge from '@material-ui/core/Badge';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import SaveIcon from '@material-ui/icons/Save';
import Avatar from '@material-ui/core/Avatar';
import {MenuButton} from './MenuButton'
import {FileMenu , EditMenu, ThemeMenu} from '../data/menus'

import {OnImport} from '../api/import/sqlimport'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,

    },
    title: {
      flexGrow: 1,

    },
    menu : {
        marginRight : 10
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
  }),
);

interface ToolbarFunction {
  onExport : () => void,
  onSave : () => void

}



export const AppToolbar: FunctionComponent<ToolbarFunction> = ({onExport , onSave }) => {
  const classes = useStyles();
  
  const isApiLoading = useSelector((state : RootState ) => state.ShareReducer.isLoading)

  function loadOpenFile() {
		var elem = document.getElementById("fileimport");
		if (elem && document.createEvent) {
			var evt = document.createEvent("MouseEvents");
			evt.initEvent("click", true, false);
			elem.dispatchEvent(evt);
		}
	}

  function onLoadFile(e: any) {

		var input = e.target;

		var reader = new FileReader();
		reader.onload = (es) => {
			let text = reader.result as string;

      if(text === null){
        text = ""
      }

      OnImport(text)
			
			e.target.value = null


		};
		reader.readAsText(input.files[0]);
	}

  function onFileMenu(id:string){
    alert(id)
    switch (id) {
      case "importmenu":
        loadOpenFile()
        break;
    
      default:
        break;
    }
  }

  return (
    <AppBar position="static" >
    <Toolbar variant="dense" style={{backgroundColor:"#5661B3"}}>
    <Avatar variant="rounded"  src="/img/erlogo.svg" style={{width:25 , height:25 , backgroundColor:"orange" , marginRight : 20 , cursor: "pointer"}} />

      <MenuButton isOpen={false} items={FileMenu} menuName="File" onClickMenu={onFileMenu} />

      <MenuButton isOpen={false} items={EditMenu} menuName="Edit" onClickMenu={onFileMenu} />

      <MenuButton isOpen={false} items={[]} menuName="Setting" onClickMenu={onFileMenu} />

      <MenuButton isOpen={false} items={ThemeMenu} menuName="Theme" onClickMenu={onFileMenu} />
   
      <MenuButton isOpen={false} items={[]} menuName="Help" onClickMenu={onFileMenu} />

      <span className={classes.title}></span>

      <input type="file" id="fileimport" style={{ display: "none" }} onChange={onLoadFile} />
      
      <IconButton aria-label="show 17 new notifications" color="inherit" >
              <Badge badgeContent={17} color="secondary">
                <NotificationsIcon />
              </Badge>
            </IconButton>
      
            <Button variant="outlined" style={{marginLeft:15}} color="inherit" 
            startIcon={(isApiLoading ? <CircularProgress style={{width:20,height:20}} color="inherit" /> : <SaveIcon/> )} onClick={onSave}>Save</Button>
                  <IconButton
              edge="end"
              aria-label="account of current user"
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
    </Toolbar>
  </AppBar>
  );
}

