import { FunctionComponent } from "react";
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';

import Drawer from '@material-ui/core/Drawer';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import IconButton from '@material-ui/core/IconButton';
import Divider from '@material-ui/core/Divider';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import { useHistory } from "react-router-dom";
import AddCircleOutlineOutlinedIcon from '@material-ui/icons/AddCircleOutlineOutlined';

const drawerWidth = 240

const useStyles = makeStyles((theme) => ({

  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    
  },
  toolbar: {
    display: 'flex',
    justifyContent: 'flex-center',
    ...theme.mixins.toolbar
  },

    drawerPaper: {
      backgroundColor : "#5661B3",
    color : "white",

        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.enteringScreen,
        }),
      },
      toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
      },

}));

export const Sidebar: FunctionComponent = () => {

    const classes = useStyles();

    const history = useHistory();

    const changePath = (p : string) => {
      history.push("/document/"+p)
    }

    return (
        <Drawer
        variant="permanent"
        className={classes.drawer}
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"

      >
        <div className={classes.toolbar}>
        <Avatar alt="Remy Sharp" src="favicon.ico"  style={{margin:10 }} />
          <Typography style={{margin:18 , fontSize:20}}>Visual Draw</Typography>
        </div>
     
        <List>
        <div>
        <ListItem button onClick={() => changePath("project")}>
            <ListItemIcon>
              <AddCircleOutlineOutlinedIcon color="inherit" style={{color:"white"}} />
            </ListItemIcon>
            <ListItemText primary="New Project" />
          </ListItem>
          <ListItem button onClick={() => changePath("filelist")}>
            <ListItemIcon>
              <DashboardIcon color="inherit" style={{color:"white"}}  />
            </ListItemIcon>
            <ListItemText primary="File List" />
          </ListItem>
          <ListItem button onClick={() => changePath("filelist")}>
            <ListItemIcon color="transparent">
              <ShoppingCartIcon style={{color:"white"}}  />
            </ListItemIcon>
            <ListItemText primary="Template"  />
          </ListItem>
          <ListItem button onClick={() => changePath("accout")}>
            <ListItemIcon>
              <PeopleIcon style={{color:"white"}} />
            </ListItemIcon>
            <ListItemText primary="Customers" />
          </ListItem>
          <ListItem button onClick={() => changePath("accout")}>
            <ListItemIcon>
              <BarChartIcon style={{color:"white"}} />
            </ListItemIcon>
            <ListItemText primary="Setting" />
          </ListItem>
          <ListItem button onClick={() => changePath("accout")}>
            <ListItemIcon>
              <LayersIcon style={{color:"white"}} />
            </ListItemIcon>
            <ListItemText primary="Logout" />
          </ListItem>
        </div>
        </List>
  
      </Drawer>
        
      )
}